import { createStore } from 'vuex'
// import axios from 'axios'
import postdata from './assets/postdata'

const store = createStore({
    state (){
        return {
            namex : 'kim',
            age : 26,
            // likes : [30,40,50],
            // 좋아요눌렀니 : false,
            more : {},
            postdata : postdata,
            likes: postdata.map(postActive => postActive.likes),
            liked: postdata.map(postActive => postActive.liked),
            heart: postdata.map(postActive => postActive.liked ? "❤" : " "),
            idx : 0,
        }
    },
    mutations : {
        changeLikes(state, idx){
            if(state.liked[idx] === false){
                state.likes[idx]++;
                state.heart[idx] = "❤"
                state.liked[idx] = true;
            }else if(state.liked[idx] === true){
                state.likes[idx]--;
                state.heart[idx] = ""
                state.liked[idx] = false;
            }
        },
        changePosts(state, postdata){
            state.posts = postdata;
            state.likes = postdata.map(postActive => postActive.likes);
            state.liked = postdata.map(postActive => postActive.liked);
            state.heart = postdata.map(postActive => postActive.liked ? "❤" : "");

        },
        setMore(state, data){
            state.more = data
        },
        이름변경(state){
            state.namex = 'kk'
        },
        나이변경(state){
            state.age ++
        },
        // 좋아요(state){
        //     if (state.좋아요눌렀니 == false){
        //         state.likes++;
        //         state.좋아요눌렀니 = true;
        //     } else {
        //         state.likes--;
        //         state.좋아요눌렀니 = false;
        //     }
                            
        // }
    },
    // actions : {
    //     getData(context){
    //         axios.get('https://codingapple1.github.io/vue/more0.json')
    //         .then((b)=>{
    //             console.log(b.data);
    //             context.commit('setMore', b.data)
    //         })
    //     },
    // },

})

export default store;