<template>
  <div @click="ff"  :class="fb + ` filter-item`" :style="`background-image:url(${올린사진})`">
    <slot>  </slot>
    <button @click="ff">버튼</button>
    </div> 
</template>

<script>
export default {
      props : {
      올린사진 : Image,
      fb : Array,
    },
    methods : {
      ff(){
        this.emitter.emit('박스클릭함', this.fb)
      }
    },
}

</script>

<style>
 .filter-item {
  width: 100px;
  height: 100px;
  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color : white;
  background-size: cover;
  background-position : center;
}
</style>