<template>
<div class="header">
  <ul class="header-button-left">
      <li>Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li v-if="step == 0" @click="step++">로그인</li>
      <li v-if="step == 1" @click="step++">Next</li>
      <li v-if="step == 2" @click="publish">글 올리기</li>
    </ul>
  <!-- <img alt="Vue logo" src="./assets/logo.png"> -->
  <!-- <img src="./assets/logo.png" class="logo" /> -->
  <ul><a href="#" class="instagram"><p class="fl-box">ㅋㅋ</p><i class="xi-instagram xi-2x" aria-hidden="true"></i><span>Instagrem</span></a></ul>
  </div>  
  
  
  <h4>안녕 {{ $store.state.namex }} gg</h4>
  <button @click="$store.commit('이름변경')">o</button>
  <p>넌 {{ $store.state.age}}</p>
  <button @click="$store.commit('나이변경')">-</button>

  <ContainerBanner @written="내가쓴글 = $event" :postdata="postdata" :step="step" :올린사진="올린사진" />
  <!-- <div @click="$store.dispatch('getData')">더보기</div> -->
  <div @click="more">더보기</div>
  <p>{{ $store.state.more}}</p>
  
  <div class="footer">
    <ul class="footer-button-plus">
      <input @change="uploaded" multiple type="file" accept="image/*" id="file" class="inputfile" />
      <label for="file" class="input-plus">+</label>
    </ul>
 </div>

 
</template>

<script>
import ContainerBanner from './components/ContainerBanner.vue'
import postdata from './assets/postdata.js'
import axios from 'axios'

export default {
  name: 'App',
  data(){
    return{
      postdata : postdata,
      더보기횟수 : 0,
      step : 0,
      올린사진 : '',
      내가쓴글 : '',
      선택한필터 : '',
      idx : 0,
    }
  },
  mounted(){
    this.emitter.on('박스클릭함',(a)=>{
      this.선택한필터 = a
    })
  },
  components: {
    ContainerBanner,
  },
  methods : {
    more(){
      axios.get(`https://codingapple1.github.io/vue/more${this.더보기횟수}.json`)
      .then(결과 => {
        this.postdata.push(결과.data);
        this.$store.commit("changePosts", this.postdata);
        this.더보기횟수++
      })
    },
    uploaded(e){
      let picture = e.target.files;
      let pictured = URL.createObjectURL(picture[0]);
      this.올린사진 = pictured
      console.log(pictured)
      this.step = 1;
    },
    publish(){
      var 게시물추가하기 = {
        name: "Fizz-K",
        userImage: "https://placeimg.com/100/100/arch",
        postImage: this.올린사진,
        likes: 0,
        date: "May 15",
        liked: false,
        content: this.내가쓴글,
        filter: this.선택한필터,
        
      };
      this.postdata.unshift(게시물추가하기);
      this.$store.commit("changePosts", this.postdata);
      this.step = 0;
    },
  }
   
}
</script>

