<template>
  <div>    
  <!-- <PostActive :postdata="postdata[0]" />
  <PostActive :postdata="postdata[1]" />
  <PostActive :postdata="postdata[2]" /> -->
  <div v-if="step == 0">
  <PostActive v-for="(pd,i) in postdata" :key="pd" :postdata="postdata[i]" :idx="i"/>
  </div>

  <div v-if="step == 1">
  <div :class="선택한필터" class="upload-image" :style="`background-image : url(${올린사진})`"></div>
  <div class="filters">

    
    <FireBox v-for="fb in fire" :key="fb" :올린사진="올린사진" :fb="fb">
      {{fb}}
    </FireBox>
    
    
  </div>
  </div>

  

  <div  v-if="step == 2">
  <div :class="선택한필터" class="upload-image" :style="`background-image : url(${올린사진})`"> </div>
  <div class="write">
    <textarea @input="$emit('written', $event.target.value)" class="write-box">write!</textarea>
  </div>
  </div>
  </div>
</template>

<script>
import PostActive from './PostActive.vue'
import FireBox from './FireBox.vue'

export default {
  data(){
    return{
      fire : [ "aden", "_1977", "brannan", "brooklyn", "clarendon", "earlybird", 
      "gingham", "hudson", "inkwell", "kelvin", "lark", "lofi", "maven", "mayfair",
       "moon", "nashville", "perpetua", "reyes", "rise", "slumber", "stinson",
        "toaster", "valencia", "walden", "willow", "xpro2"],
        선택한필터 : '',
    }
  },
  mounted(){
    this.emitter.on('박스클릭함', (a)=>{
      this.선택한필터 = a
    })
  },
  components : {
    PostActive,
    FireBox,
  },
  props : {
    postdata : Array,
    step : Number,
    올린사진 : Image,
  }
}
</script>

<style>
.upload-image{
width: 100%;
height: 450px;
background: cornflowerblue;
background-size : cover;
}
.filters{
overflow-x:scroll;
white-space: nowrap;
} 
.filter-1 {
width: 100px;
height: 100px;
background-color: cornflowerblue;
margin: 10px 10px 10px auto;
padding: 8px;
display: inline-block;
color : white;
background-size: cover;
}
.filters::-webkit-scrollbar {
height: 5px;
}
.filters::-webkit-scrollbar-track {
background: #f1f1f1; 
}
.filters::-webkit-scrollbar-thumb {
background: #888; 
border-radius: 5px;
}
.filters::-webkit-scrollbar-thumb:hover {
background: #555; 
}
.write-box {
border: none;
width: 90%;
height: 100px;
padding: 15px;
margin: auto;
display: block;
outline: none;
}
</style>